        width: 100%;
        margin: 0;
        transform: none;
        position: static;
        overflow-y: auto
    }

    .modal-feedback .modal-content {
        border: none
    }
}

.modal-feedback .modal-content {
    background: #fff
}

.modal-feedback .modal-header {
    padding: 0;
    border: none
}

.modal-feedback .modal-header .modal-title {
    text-align: center;
    padding: 25px;
    font-size: 16px;
    line-height: 19px;
    background-color: #eef9b5
}

.modal-feedback .close-btn {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    width: 40px;
    height: 40px;
    font-size: 0;
    background: 0 0;
    border: none;
    outline: 0;
    cursor: pointer
}

.modal-feedback .close-btn:after,
.modal-feedback .close-btn:before {
    position: absolute;
    right: 11px;
    top: 23px;
    content: "";
    width: 22px;
    height: 1px;
    background-color: #000;
    transform: rotate(45deg)
}

.modal-feedback .close-btn:after {
    transform: rotate(135deg)
}

.modal-feedback .modal-body {
    padding: 0
}

.modal-feedback .modal-body form {
    padding: 40px
}

.modal-feedback .modal-body .image {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 75px;
    height: 75px;
    overflow: hidden
}

.modal-feedback .modal-body .image img {
    display: block;
    width: 100%;
    height: auto
}

.modal-feedback .modal-body .form-group {
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    margin-bottom: 10px
}

.modal-feedback .modal-body .form-group label {
    margin: 0;
    color: #736972;
    font-weight: 400
}

.modal-feedback .modal-body .form-group input,
.modal-feedback .modal-body .form-group textarea {
    width: 100%;
    height: auto;
    margin: 3px 0 7px;
    padding: 10px;
    color: #44404a;
    line-height: 1;
    border-radius: 4px;
    border: 1px solid #cdcdcd;
    outline: 0
}

.modal-feedback .modal-body .form-group textarea {
    height: 100px;
    resize: none
}

.modal-feedback .modal-body .form-group .help-block.help-block-error:empty {
    display: none
}

.modal-feedback .modal-body .form-group .btn,
.modal-feedback .modal-body .form-group .fasc-button {
    display: block;
    text-align: center;
    line-height: 1;
    min-width: 180px;
    margin: 20px auto 0;
    padding: 13px 20px;
    border: none;
    border-radius: 25px;
    outline: 0
}

.modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 999;
    background-color: #000;
    display: none
}

.modal-backdrop.in {
    opacity: .5;
    display: block
}

.modal {
    position: fixed;
    z-index: 1000;
    top: 50%;
    left: 50%;
    display: none;
    background-color: #fff;
    transform: translate(-50%, -50%);
    width: 500px
}

.modal.modal-background {
    box-shadow: 0 0 100px 100px rgba(0, 0, 0, .3)
}

.modal.animate {
    display: block;
    z-index: -9999
}

@media (max-width:659px) {
    .modal {
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        transform: none;
        max-height: 100vh;
        overflow-y: auto
    }
}

.modal.in,
.modal.show {
    display: block
}

.modal.bounceIn {
    position: absolute;
    display: block;
    transform: translate(0, 0);
    margin: 0 0 0 -50%
}

.modal.fade.show {
    box-shadow: 0 0 1000px 1000px rgba(0, 0, 0, .7);
    z-index: 999
}

.modal-overlay {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5)
}

.modal-overlay.show {
    display: block
}

.modal-wrapper {
    margin: 0 auto;
    width: 300px
}

@media (max-width:659px) {
    .modal-wrapper {
        width: 280px
    }
}

.modal-header {
    padding: 20px 0;
    background-color: #edf9b4;
    text-align: center
}

.modal-header p {
    margin: 0;
    font: 400 16px/19px "Open Sans", Arial, sans-serif
}

.modal-header .title {
    font: 400 17px/normal "Open Sans", Arial, sans-serif;
    text-transform: uppercase
}

.modal-close {
    position: absolute;
    right: 5px;
    top: 5px;
    width: 40px;
    height: 40px;
    background: 0 0;
    border: 0;
    outline: 0;
    cursor: pointer
}

.modal-close:after,
.modal-close:before {
    position: absolute;
    right: 8px;
    top: 19px;
    content: "";
    width: 22px;
    height: 1px;
    background-color: #44404a
}

.modal-close:before {
    transform: rotate(45deg)
}

.modal-close:after {
    transform: rotate(135deg)
}

.modal-body {
    padding: 40px 0
}

.modal-navigation {
    text-align: center
}

.modal-navigation ul {
    display: flex;
    margin: 0;
    padding: 0
}

.modal-navigation li:not(:last-child) {
    margin-right: 15px
}

.modal-navigation__item {
    padding-bottom: 9px;
    color: #a0a0a0;
    font: 400 17px/normal "Open Sans", Arial, sans-serif;
    text-transform: uppercase;
    text-decoration: none
}

.modal-navigation__item.active {
    color: #44404a;
    border-bottom: 2px solid #44404a
}

.modal-scrollable {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0
}

.modal-scrollable .modal {
    display: block
}

.profile-image__item {
    display: flex;
    justify-content: flex-start;
    align-items: center
}

.profile-image__item .image {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #d6d6d6;
    margin: 0 30px 0 0
}

.profile-image__item .image img {
    width: 100%;
    min-height: 100%
}

.profile-image__item .btn,
.profile-image__item .fasc-button {
    margin: 0
}

.profile-subscribe {
    padding: 20px 0
}

.profile-social ul {
    display: flex;
    justify-content: flex-start;
    list-style-type: none;
    margin: 0;
    padding: 0
}

@media (max-width:989px) {
    .profile-social ul {
        flex-wrap: wrap
    }
}

.profile-social ul li {
    margin: 0 15px
}

@media (min-width:660px) and (max-width:989px) {
    .profile-social ul li {
        margin: 15px 15px 15px 0;
        width: 45%
    }
}

@media (max-width:659px) {
    .profile-social ul li {
        margin: 15px auto;
        width: 50%
    }
}

@media (min-width:660px) {
    .profile-social ul li:first-child {
        margin-left: 0
    }
}

@media (min-width:660px) {
    .profile-social ul li:last-child {
        margin-right: 0
    }
}

.profile-social ul li .social {
    cursor: pointer;
    display: flex;
    align-items: center
}

.profile-social ul li .social-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    flex: 0 0 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 10px 0 0
}

.profile-social ul li.disabled .fasc-button,
.profile-social ul li.disabled .solid {
    background-color: #d6d6d6
}

